 * animation swirl-in-fwd
 * ----------------------------------------
 */
@keyframes swirl-in-fwd {
  0% {
    transform: rotate(-540deg) scale(0);
    opacity: 0;
  }
  100% {
    transform: rotate(0) scale(1);
    opacity: 1;
  }
}
@keyframes slide-in-left {
  0% {
    transform: translateX(-1000px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes fade-in-left {
  0% {
    transform: translateX(-50px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes fade-in-right {
  0% {
    transform: translateX(50px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.formCotainer {
  position: relative;
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--white);
}

.formWrapper{
  max-width: 80vw;
  min-width: 40vw;
  height: 100vh;
  transition: all 2s ease-in-out;
  display: flex;
  flex-direction: column;
  padding: 40px 20px;
  transform: scaleX(1);
}
.formIframe{
  width: 80%;
}
@media (max-width: 1024px) {
  .formWrapper{
    max-width: 80vw;
    min-width: 30vw;
    height: 100%;
  }
  .formCotainer h1,  .formWrapper p{
    font-size: 3em;
  }
  .formWrapper h1,   .formWrapper p{
    font-size: 3em;
  }
}
@media (max-width: 768px) {
  .formWrapper{
    margin-top: 30px;
    max-width: 100vw;
    min-width: 100vw;
    height: 100vh;
  }
  .formWrapper h1, .formWrapper p{
    display: none;
  }
}
@media (max-width: 376px) {
  input#firstname{
    margin-bottom: 16px;
  }
  input#lastname{
    margin-top: 8px;
  }
}

.formItemWrapper{
  display: flex;
  flex-wrap: wrap;
  margin: 8px 0px;
}

.formItem{
  flex: 1 1;
  display: flex;
  flex-direction: column;
}
.formItem, h1, .formItem p{
  margin: 0 5px;
}

.formItem input{
  font-weight: 500;
  padding: 5px;
  border-radius: 5px;
  color: black;
  border: 1px solid #ccc;
}
.formItem label{
  color: var(--black);
}
.formWrapper h1, .formItem p{
  color: var(--black);
}

.formItem button {
  width: 100px;
  padding: 8px;
  align-self: flex-end;
  border-radius: 5px;
  border: 1px solid #ccc;
}
.formItem textarea {
 border: 1px solid #ccc;
 border-radius: 5px;
}


.about-container .conteudo {
  color: #777;
  background-color: var(--white);
  text-align: justify;
  padding: 5vh 5vw;
}

.about-container .caixa1, .caixa2, .caixa3, .caixa4 {
  transition: all 5s ease-in-out;
  position: relative;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0.8;
}

.about-container p {
  margin-bottom: 20px;
}

.about-container h3 {
  text-transform: uppercase;
  color: var(--black);
  text-align: center;
  padding-bottom: 10px;
}

/*efeito parallax*/
.about-container .conteudo-titulo {
  display: flex;
  align-items: center;
  width: 100%;
  height: 500px;
}

.conteudo-titulo span.titulo {
  background-color: var(--black);
  padding: 18px;
  font-weight: 600;
  font-size: 4em;
  letter-spacing: 5px;
  color: var(--white);
  text-transform: uppercase;
  margin: 0 auto;
}
